*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body{
  scroll-behavior: smooth;
}
#Main{
  background-image: linear-gradient(180deg,#eff6ff,#dce9fa);
  height: 700px;
  display: flex;
}
.innermaindiv{
  display: grid;
  grid-template-columns: repeat(6,1fr);
  padding: 2% 5% 0 5%;
  overflow: hidden;
}
.FirstC{
  color: black;
  text-align: center;
display: flex;
justify-content: center;
flex-direction: column;
grid-column: 1/4;
text-align: start;
}
.FirstC h1{
  font-size: 45px;
}
.FirstC button{
  width: fit-content;
  padding: 20px 105px;
  font-size: 17px;
  cursor: pointer;
  margin-top: 25px;
  border: none;
  background-color: brown;
  color: white;
  background-image: linear-gradient(103deg,#aaf1ff,#807dff,#da23ff,#ba5fff);
}
.navitem div{
  margin: auto 10px;
  cursor: pointer;
  background-color: #7379b9;
  min-width: 93px;
  text-align: center;
  padding: 5px 20px;
  color: white;
  border-radius: 50px;
  transition: 0.2s ease-in-out;
}
.navitem div:hover{
  background-color: #60659f;
  transition: 0.2s ease-in-out;
}
.MainImgDiv{
  grid-column: 4/7;
}
.MainImgDiv img{
  width: 100%;
  height: 100%;
  object-position: bottom;
  object-fit: scale-down;
}
.App-header {
  font-size: calc(10px + 2vmin);
  color: white;
}
.Navbar{
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color:#eff6ff;
  color: #000000;
  padding: 1% 10%;
}
#Logo{
  width: 50px;
  height: 100%;
}
#Logo img{
width: 100%;
height: 100%;
object-fit: scale-down;
}
#About{
  width: 100%;
  background-color: #000000;
  color: white;
  padding: 5% 15%;
  text-align: start;
}

#About span {
  font-size: 40px;
  font-weight: 500;
  margin-bottom: 20px;
  display: block;
  text-align: center;
}
#About p {
  font-size: 20px;
  font-weight: 200;
  margin: 40px 0;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 400;
}
#About p ul{
  margin: 10px 0;
  padding: 0 40px;
}
#Project{
  text-align: center  ;
  background-color: #dce9fa;
  padding: 5% 0;
}
#Project h1{
  background-color: #000000;
  width: fit-content;
  margin: 0 auto 45px auto;
  padding: 15px 70px;
  color: white;
  font-size: 24px;
  font-weight: 500;
}
#Project h4{
  font-weight: 400;
  font-size: 20px;
  margin:20px 0 ;
}
.siteName div{
  text-align: start;
  width: 40%;
  margin: 0 15px;
  padding: 5px;
  display: flex;
  justify-content: space-between;  
}
.siteName div p{
margin: 5px 0;
width: 49%;
background-color: rgb(153, 210, 228);
color:#1e2d2d;
border-radius: 5px;
text-align: center;
}
.siteName{
  display: flex;
  justify-content: center;
}
.projectsimgs a img::before{
  background-color: antiquewhite;
  content:'';
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}
.siteName span{
  display: block;
  text-align: center;
  width: 40%;
  margin-top: 15px;
  padding: 5px;
}
.siteName p{
  display: block;
  text-align: start;
  width: 40%;
  margin: 0 15px;
  background-color: #ab93c2;
  padding: 5px;
}
.siteName a{
  display: block;
  width: 40%;
  margin: 0 15px;
  background-color: #ab93c2;
  padding: 5px;
}
.projectsimgs{
  display: flex;
  justify-content: center;
  }
.projectsimgs a{
display: block;
width: 39%;
aspect-ratio: 1.8/1;
margin: 0 22px;
}
.projectsimgs a img{
  width: 100%;
  height: 100%;
  border-radius:  7px  7px 0 0;
  object-fit: cover;
  box-shadow: 0px 0px 2px black;
  position: relative;
  transition: 0.3s ease-in-out;
}

.MVC{
  width: 100% !important; 
}
#Skills{
  background-image: linear-gradient(360deg,#eff6ff,#dce9fa);

}
.SkillsSec{
  padding: 2% 5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.SkillsSec div{
  color: rgb(0, 0, 0);
  width: 30%;
  margin: 1%;
  text-align: center;
  padding: 15px;
  aspect-ratio: 1.7/1;

}
#Skills h1{
  padding: 55px 15px 15px 15px;
  border-bottom: 3px solid black;
  width: fit-content;
  margin: 0 auto;
  font-weight: 400;
  text-align: center;
  cursor: default;

}
.SkillsSec div div{
height: 60%;
width: 100%;
}

.SkillsSec div div img{
width: 100%;
height: 100%;
object-fit: scale-down;
}
.SkillsSec div p{
width: 80%;
margin: 20px auto 0 auto;
height: 20%;
}
#Contact{
  display: flex;
  width: 100%;
  justify-content: center;
  padding: 1% 0%;
}
#Contact a{
width: 200px;
display: block;
padding: 7px 25px;
text-align: center;
border-radius: 50px;
cursor: pointer;
margin: 0 1%;
color: white;
text-decoration: none;
}
#Contact a img{
width: 100%;
height: 100%;
object-fit: cover;
cursor: pointer;
}
.WS{
  background-color: #445f50;
}
.OL{
  background-color: #682d2d;
}
.LI{
  background-color: #3e6591;
}
.ContactDiv{
  background-color: rgb(0, 0, 0);
  color: white;
  padding: 1% 0 ;
  background-image: linear-gradient( 24deg,#34364f,#2c2f48,#5c6397);
  border-top: 2px solid rgb(129, 129, 129);
}
.ContactDiv h1{
  text-align: center;
}
.ContactDiv h1{
  font-weight: 400;
  font-size: 16px;
}
.ColomN p{
margin: 15px 0;
cursor: default;
}
#Leproject{
  display: grid;
grid-template-columns: 1fr 1fr;
padding: 4%;
text-align: center;
}
.prJ h1{
  font-size: 20px;
  font-weight: 500;
  text-align: center;
  background-color: black;
  width: fit-content;
  margin: 0 auto;
  color: white;
  padding: 10px 30px;
  cursor: default;
  background-image: linear-gradient(103deg,#aaf1ff,#807dff,#da23ff,#ba5fff);
}
#Leproject .ColomN{
  width: 80%;
  margin: 0 auto;
}
.ProjectIMG{
  aspect-ratio: 1.8/1;
  display: flex;
  padding: 0 2%;
}
.ProjectIMG img{
  height: 100%;
  width: 100%;
  border-radius: 5px 5px 0 0;
  object-fit: cover;  
}
#Leproject .ancor{
  background-color: #ab93c2;
  display: block;
  padding: 5px;
  cursor: pointer;
  background-color: #1c1c1c;
  color: white;
}
.prJ{
  background-image: linear-gradient(180deg,#eff6ff,#dce9fa);
  padding: 4% 0;
}
.Tech{
  display: grid;
  grid-template-columns: repeat(auto-fit,minmax(49%,1fr));
  justify-content: center;
}
.Tech p{
  background-color: #3c6d67;
  color: #323a42;
  background-color: #8bc3d9;
  border-radius: 5px;
  margin: 5px;
  padding: 7px 0;
}
.navitem{
  display: flex;
}


@media (max-width: 600px) {
  body {
    font-size: 12px !important;
  }
  #Main{
    height: 145px;
  }
  #Leproject{
    grid-template-columns: 1fr;
  }
  #Leproject .ColomN{
    width: 100%;
  }
  .SkillsSec div{
    width: 100%;
  }
  .FirstC h1{
    font-size: 15px;

  }
  .FirstC button{
    padding: 7px 17px;
    font-size: 12px;
    margin-top: 6px;
  }
  #Logo{
    width: 35px;
  }
  .ContactDiv{
    padding: 4% 0;
  }
  .ContactDiv h1{
    margin-bottom: 5px;
  }
  .navitem div{
margin: auto 2px;
min-width: 54px;
text-align: center;
padding: 5px 10px;
font-size: 11px;
  }
  #About{
    padding: 5% 5%;
  }
  #About p{
    font-size: 13px;
  }
  #About span {
    font-size: 25px;

  }

}